<template>
  <section class="content">
    <div class="error-page">
      <h2 class="headline text-yellow"> 404</h2>

      <div class="error-content"  style="padding-top: 1px;">
        <h3><i class="fa fa-warning text-yellow"></i> Oops! Page not found.</h3>
        <p>
          We could not find the page you were looking for. <br>
        </p>
        <p>
          <a href="javascript:void (0)" @click="$router.go(-2)"><b>Return to the previous page ></b></a> <br>
        </p>
        <p>
          <router-link href="javascript:void (0)" to="/" >Return to the front desk page</router-link> <br>
        </p>
      </div>
    </div>
    </section>
</template>

<script>
  export default {
    name: "notound",
  }
</script>
<style scoped>
  .content{
    position: absolute;
    top:50%;
    left:50%;
    width:100%;
    transform:translate(-50%,-50%);
    text-align: center;
  }
</style>



